.home-nav-bar {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 12%;
    width: 100%;
    background-color: #191a1e;
}

.logo-container {
    width: 65%;
}

.nav-bar-item-container {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 25px;
}

.home-nav-bar-item {
    background-color: transparent;
    border: transparent;
    color: #fff;
    font-size: 18px;
    letter-spacing: 3px;
}